body {
  color: #ececec;
  background-color: #1e1e1e;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  display: flex;
}

#dajjal {
  background: #2c2c2c;
  border: 1px solid #444;
  border-radius: 20px;
  max-width: 700px;
  margin: 0 auto;
  padding: 15px 25px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  box-shadow: 0 10px 20px #0003;
}

#dajjal:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px #0006;
}

h1, h2 {
  text-align: center;
  color: #4fc3f7;
  margin-bottom: 25px;
  font-weight: bold;
}

input, button {
  box-sizing: border-box;
  border: 1px solid #555;
  border-radius: 30px;
  width: calc(100% - 50px);
  margin: 15px auto;
  padding: 15px;
  font-size: 18px;
  transition: border-color .3s, background-color .3s, transform .3s;
  display: block;
}

input:focus, button:focus {
  border-color: #4fc3f7;
  outline: none;
}

button {
  color: #1e1e1e;
  cursor: pointer;
  background-color: #4fc3f7;
  border: none;
  transition: background-color .3s, transform .3s;
}

button:hover {
  background-color: #29b6f6;
  transform: translateY(-3px);
}

img {
  border-radius: 15px;
  max-width: 60%;
  margin: 0 auto;
  display: block;
  box-shadow: 0 5px 10px #0000004d;
}

ul {
  padding: 0;
  list-style-type: none;
}

li {
  background: #333;
  border-radius: 20px;
  margin: 15px 0;
  padding: 20px;
  transition: background .3s, transform .3s;
  box-shadow: 0 3px 6px #0003;
}

li:hover {
  background: #4f4f4f;
  transform: translateY(-3px);
}

label {
  text-align: center;
  color: #9e9e9e;
  margin-top: 15px;
  display: block;
}

input[type="radio"] {
  display: none;
}

input[type="radio"] + label img {
  cursor: pointer;
  transition: transform .3s, border .3s;
}

input[type="radio"]:checked + label img {
  border: 3px solid #4fc3f7;
  transform: scale(1.1);
}

.character-selection {
  justify-content: space-around;
  align-items: center;
  margin-bottom: 25px;
  display: flex;
}

@media (width <= 700px) {
  #dajjal {
    margin: 20px;
    padding: 10px 20px;
  }

  input, button {
    width: calc(100% - 40px);
  }

  .character-selection {
    flex-direction: column;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#dajjal {
  animation: 1s ease-in-out fadeIn;
}

button:active {
  transform: scale(.98);
}

.tooltip {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  color: #1e1e1e;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #4fc3f7;
  border-radius: 6px;
  width: 150px;
  margin-left: -75px;
  padding: 5px;
  transition: opacity .3s;
  position: absolute;
  bottom: 125%;
  left: 50%;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

